h1,
h2,
h3,
h4,
h5,
h6,
li > a{
  font-family: Dosis;
}
div, p, span{
  font-family: Roboto;
}

.logo {
  width: 120px;
  height: 31px;
  background-color: #ffffff;
  margin: 16px 24px 16px 0;
  float: left;
  font-size: 22px;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #333333;
}
.ant-layout-header {
  background-color: white;
}

.dog-breeder{
  padding: 10px 30px;
  text-align: center;
  background-image: linear-gradient(to right, #71ebe8, #fffd8b);
}
.ant-menu-dark {
  display: flex;
  margin-top: 17px;
  background: transparent;
}
.ant-menu-item-selected {
  background: transparent !important;
}
.ant-menu-item {
  padding: 5px 2px;
  margin: 0 30px 0 30px;
  font-size: 18px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #333333 !important;
}
.ant-menu-item:hover {
  border-bottom: 1.5px solid #71ebe8 !important;
}
.white-bg {
  margin-top: 64px;
  background-color: white;
}
.top-nav {
  float: right;
}

.heading{
  font-size: 48px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #333333;
  margin: 30px 0 50px 0;
}
.how-to-use{
  font-size: 18px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  color: #333333;
  margin-bottom: 40px;
}
.content-box {
  width: 350px;
  margin: 0 auto;
}
.content-box h3 {
  font-size: 24px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #333333;
}
.content-box img {
  width: 100%;
  height: 255px;
  object-fit: cover;
  margin-bottom: 20px;
}
.content-box .ant-select-selection {
  height: 40px;
  border-radius: 5px;
  margin-bottom: 40px;
  background-color: #f6f6f6;
}
.content-box .ant-select-selection__rendered {
  line-height: 39px;
}
.content-box p {
  color: #333333;
  font-size: 18px;
  line-height: 24px;
  margin: 0 0 60px 0;
  text-align: justify;
}
.ant-btn {
  border: 0;
  height: 48px;
  padding: 0 40px;
  font-size: 20px;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #ffffff;
  border-radius: 5px;
  box-shadow: 0 1px 3px 0 rgba(161, 161, 161, 0.25);
  background-image: linear-gradient(to right, #71ebe8, #fffd8b);
}
.m-b-50 {
  margin-bottom: 50px;
}
.gray-bg {
  padding: 55px 0 50px 0;
  background-color: #fdfdfd;
}
.p-t-15 {
  padding-top: 15px;
}
.p-l-70 {
  padding-left: 70px;
}
h5 {
  font-size: 18px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #ffffff;
  margin: 0;
  text-align: left;
}

@media (min-width: 320px) and (max-width: 767px) {

  .ant-menu-dark {
    display: none;
  }
  .dog-breeder {
    padding: 10px 10px;
  }
  .heading {
    font-size: 40px;
    margin: 10px 0 20px 0;
  }
  .white-bg {
    padding: 0 10px;
  }
  .white-bg .ant-col-offset-6 {
    margin-left: 0;
  }
  .white-bg .ant-col-offset-4 {
    margin-left: 0;
  }
  .how-to-use {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .content-box {
    width: 100%;
  }
  .content-box .ant-select-selection {
    margin-bottom: 0;
  }
  .content-box p {
    font-size: 16px;
    text-align: center;
    margin: 15px 0 30px 0;
  }
  .ant-btn {
    padding: 0 20px;
  }
  .m-b-50 {
    margin-bottom: 30px;
  }
  .gray-bg {
    padding: 20px 10px 0 10px;
  }
  .p-l-70 {
    padding-left: 0;
    padding-bottom: 10px;
  }

}